import Vue from 'vue'
import App from './App'
import router from './router'
import anime from 'animejs'
import store from './store/index'
//import * as d3 from 'd3';

Vue.config.productionTip = false

window._anime = anime;



/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



