//import Vue from 'vue'
import VueRouter from 'vue-router'
import articles from '../../articles/articlesMap'
const _import = require('./_import_' + process.env.NODE_ENV);
//路由懒加载仅在生产环境下执行


//Vue.use(Router);

const layOut = _import('mainContent/index');
const blogRouters = [
    {
      path: '/blog/index',
      name: 'blog-list',
      component: _import('mainContent/blog/index')
    }
];

// for(let mdFileName in articles){
//   let name = mdFileName.replace(/\.md/,'');
//   let mdRouterObj = {
//     path:'/blog/'+name,
//     name: name,
//
//   };
//   blogRouters.push()
// }

export default new VueRouter({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'home',
      component: _import('home')
    },
    {
      path: '/blog',
      name: 'blog',
      component:layOut,
      redirect: '/blog/index',
      children:[
          {
            path: '/blog/index',
            name: 'blog-list',
            component: _import('mainContent/blog/index'),
          },
          {
            path: '/blog/article/:blogName',
            name: 'blog-article',
            component: _import('mainContent/blog/article'),
          }
      ]
    },
    {
      path: '/project',
      name: 'project',
      component:layOut,
      redirect: '/project/index',
      children:[
        {
          path: '/project/index',
          name: 'project-list',
          component: _import('mainContent/project/index'),
        }
      ]
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    // return 期望滚动到哪个的位置
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})



